.button {
  display: block;
  padding: 0.5em 1em;
  border: none;
  background: #222;
  color: #FFF;
  border-radius: 6px;
  font-size: 1.3rem;
  line-height: 2rem;
  cursor: pointer; }
  .button:hover, .button:focus {
    background: #e44; }

.button--primary {
  background: #e44;
  font-weight: bold; }

.button--inline {
  display: inline-block;
  width: auto;
  margin-left: 0.2em; }
  @media (min-width: 1024px) {
    .button--inline {
      margin-left: 1em; } }

.button--link {
  background: none;
  color: #000;
  text-decoration: underline;
  padding: 0;
  font-size: 1.1rem;
  line-height: 1em; }
  .button--link:hover, .button--link:focus {
    background: none;
    text-decoration: none; }

.button--icon {
  padding: 0.5em; }
  .button--icon span:last-child {
    margin-left: 0.5em; }



