.modal__body {
  position: fixed;
  z-index: 1;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  overflow: auto;
  background: #FFF;
  max-height: 100%;
  min-width: 95%;
  border-top: 6px solid #0b5;
  border-radius: 6px; }
  @media (min-width: 1024px) {
    .modal__body {
      min-width: 75%; } }
  @media (min-width: 1400px) {
    .modal__body {
      min-width: 50%; } }

.modal__content {
  padding: 1em 3em; }

.modal__title {
  font-size: 2.5rem;
  line-height: 1.2em; }

.modal__footer {
  padding: 1em;
  background: #0b5; }
  .modal__footer .button {
    margin-left: auto; }

.modal__mask {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.6); }

.modal[aria-hidden="false"] {
  display: block; }

.modal[aria-hidden="true"] {
  display: none; }



