.shape-defs {
  width: 0;
  height: 0; }

.shape {
  stroke-width: 20px;
  stroke: #000;
  vertical-align: middle;
  -ms-flex: 0 0 33.33%;
      flex: 0 0 33.33%; }

.pattern--red {
  stroke: #e44; }

.pattern--green {
  stroke: #0b5; }

.pattern--yellow {
  stroke: #fa0; }

.shape--red {
  fill: #e44; }
  .shape--red.shape--empty {
    fill: none;
    stroke: #e44; }
  .shape--red.shape--granular {
    fill: url(#pattern-red); }

.shape--green {
  fill: #0b5; }
  .shape--green.shape--empty {
    fill: none;
    stroke: #0b5; }
  .shape--green.shape--granular {
    fill: url(#pattern-green); }

.shape--yellow {
  fill: #fa0; }
  .shape--yellow.shape--empty {
    fill: none;
    stroke: #fa0; }
  .shape--yellow.shape--granular {
    fill: url(#pattern-yellow); }



